<template>
    <div class="login_form to_center">
        <h1>Login to the best application in the University</h1>
        <el-input placeholder="Email"
                  v-model="email"
                  :disabled="loading"/>
        <el-button type="primary"
                   @click="submit">
            Submit
        </el-button>
    </div>
</template>

<script>
  import {mapActions} from 'vuex';

  export default {
    name: 'Login',
    data() {
      return {
        email: '',
        loading: false,
      }
    },
    methods: {
      ...mapActions(['loginUser']),
      async submit() {
        this.loading = true;

        try {
          await this.loginUser(this.email);

          await this.$router.push({name: 'home'})
        } catch (e) {
          this.$notify({
            title: 'Error',
            message: e.message ? e.message : 'Error',
            type: 'error'
          });
        } finally {
          this.loading = false;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
    .login_form {
        width: 50%;
        margin: 0 auto;

        h1 {
            text-align: center;
        }

        button {
            margin-top: 20px;
            width: 100%;
        }
    }
</style>
